@font-face {
  font-family: 'Montserrat';
  font-style:  normal;
  font-weight: 700;
  font-display: swap;
  src: url("Montserrat-Bold.woff2") format("woff2"),
       url("Montserrat-Bold.woff") format("woff"),
       url("Montserrat-Bold.ttf") format("ttf");
}

